import AOS from "aos";
import "aos/dist/aos.css";
import "./style.css";

import ReactPlayer from "react-player";

AOS.init({});

AOS.refresh();

function App() {
  return (
    <div className="container">
      <div
        data-aos="fade-down"
        data-oas-duration="1000"
        className="content one"
        
      >
        <div className="opacity"></div>
        <img src={require("./asset/Saber.jpg")} width="1390" height="650" />
        <div
          className="card-item"
          data-aos="fade-left"
          data-aos-mirror="true"
          data-oas-duration="1000"
          data-aos-delay="1000"
        >
          <p>
            <h1>Lorem</h1>
            Lorem Ipsum is simply dummy text of the printing and typesetting
            industry. Lorem Ipsum has been the industry's standard dummy text
            ever since the 1500s, when an unknown printer took a galley of type
            and scrambled it to make a type specimen book. It has survived not
            only five centuries, but also the leap into
          </p>
        </div>
      </div>
      <div data-aos="fade-up" data-oas-duration="1000" className="content two">
        <div className="vd-box">
          <ReactPlayer
            className="video"
            url={require("./asset/vd.mp4")}
            width=""
            height="125vh"
            loop
            config={{
              file: {
                attributes: {
                  autoPlay: true,
                  muted: true,
                },
              },
            }}
          />
          <div className="vd-title">
              <h1>LIN MINH HUYN THOI L G?</h1>
              <p>
                Lin Minh Huyn Thoi l mt tr chi chin thut ng i ni
                hai i vi nm v tng mnh m s i u nhau nhm mc tiu
                ph hy cn c i phng. Chn mt trong hn 150 v tng 
                to ra nhng pha thi u xut thn, h gc i th, san phng
                tr ch v hng n chin thng vinh quang.
              </p>
            </div>
        </div>
      </div>
      <div className="content three">
        <div>Third Page</div>
      </div>
      <div className="content four">
        <div>End Page</div>
      </div>
    </div>
  );
}

export default App;
